/* Tr === twitter */
.cardTr {
  height: 100vh;
  background: var(--trBackgroundNoImg);
  color: var(--txt);
}

.cardWithImg::before {
  content: '';
  width: 100vw;
  height: 100vh;
  background-image: var(--before);
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
  filter: blur(5px);
  position: fixed;
  top: 0;
  left: 0;
}

.cardBody {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--txt);
  margin: 5rem 1rem;
  font-size: 35px;
  font-weight: bold;
  text-align: left;
}

.cardBodyNoImg {
  display: grid;
  justify-content: center;
  align-items: center;
  margin: 0 5rem;
  font-size: 35px;
  text-align: center;
}

.cardBodyWithImg {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--txt);
  margin: 5rem 1rem;
  font-size: 35px;
  font-weight: bold;
  text-align: left;
}

.content {
  margin: 2rem 0;
  background: rgba(0, 0, 0, 0.5);
  padding: 1.5rem;
}

.contentNoImg {
  margin: 10rem 0 6rem 0;
  font-weight: normal;
}

.txtHashtag,
.txtMention {
  color: var(--tr);
  font-weight: bold;
}

.cardBodyNoImg .txtHashtag,
.cardBodyNoImg .txtMention {
  color: var(--black);
  font-weight: bold;
}

.cardBodyWithImg .txtRetweet,
.cardBodyNoImg .txtRetweet {
  display: none;
}

.reTweet {
  color: var(--txt);
  font-weight: bold;
  margin: 1rem 0 0 1rem;
}

.txtColorPicker {
  color: var(--change, red);
}

.userCard {
  display: flex;
  align-items: center;
}

.footerCard {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 5rem;
}

.hashtag {
  color: var(--txt);
  font-weight: 700;
  margin: 1rem 1rem 0 0;
}

.logoUser {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-top: 1rem;
  background-color: #fff;
}

.cardImg {
  margin: 0 0 0 5rem;
}

.getImg {
  display: flex;
  width: 450px;
  height: 400px;
}

.hideImg {
  display: none;
}

@media screen and (max-width: 768px) {
  .cardImg {
    margin: 0;
  }

  .cardBodyWithImg {
    display: block;
    font-size: 25px;
  }
  .getImg {
    display: none;
  }
}
